import React, { Component } from 'react';
import Controls from './Controls';
import Grid from './Grid';

/*
 TODO: implement a getNeighbours function to return the Neighbours of a given
       cell
 TODO: stop button
 TODO: input to control time interval
 TODO: tick() given the set of alive and dead cells, apply rules to calculate
       new set of alive and dead cells.
*/

function intersection(setA, setB) {
    var _intersection = new Set();
    for (var elem of setB) {
        if (setA.has(elem)) {
            _intersection.add(elem);
        }
    }
    return _intersection;
}

function parseAliveStr(s) {
    const re = /^pos-(\d)-(\d)$/;
    const m = re.exec(s);
    return {
        i: m[1],
        j: m[2],
    };
}

const DEFAULT_STATE = {
    started: false,
    initialCount: 50,
    generation: 0,
    interval: 500,
    size: {
        rows: 10,
        cols: 10,
    },
    alive: new Set(),
};


class GoL extends Component {
    constructor(props){
        super(props);
        this.state = DEFAULT_STATE;
        this._timer = null;

        this.toggleAlive = this.toggleAlive.bind(this);
        this.startGame = this.startGame.bind(this);
        this.makeRandomBoard = this.makeRandomBoard.bind(this);
        this.changeInitialCountHandler = this.changeInitialCountHandler.bind(this);
        this.initializeBoard = this.initializeBoard.bind(this);
        this.stopGame = this.stopGame.bind(this);
        this.changeNumColsHandler = this.changeNumColsHandler.bind(this);
        this.changeNumRowsHandler = this.changeNumRowsHandler.bind(this);
        this.reset = this.reset.bind(this);
        this.tick = this.tick.bind(this);
        this.changeIntervalHandler = this.changeIntervalHandler.bind(this);
    }

    getNeighbours(i, j) {
        const { cols: nCols, rows: nRows } = this.state.size;
        const hMin = Math.max(i - 1, 0);
        const hMax = Math.min(i + 1, nCols - 1);
        const vMin = Math.max(j - 1, 0);
        const vMax = Math.min(j + 1, nRows - 1);

        const neighbours = new Set();
        for (let x = hMin; x <= hMax; x++) {
            for (let y = vMin; y <= vMax; y++) {
                if (x !== i || y !== j){
                    neighbours.add(`pos-${x}-${y}`);
                }
            }
        }

        return neighbours;
    }

    makeRandomBoard(x) {
        const { cols: nCols, rows: nRows } = this.state.size;
        let i;
        let j;
        const cells = new Set();
        for (let n = 0; n < x; n++) {
            i = Math.floor(Math.random() * nCols);
            j = Math.floor(Math.random() * nRows);
            cells.add(`pos-${i}-${j}`);
        }
        this.setState({
            alive: cells,
        });
    }

    startGame() {
        this.setState({
            started: true,
        });

        this._timer = setInterval(() => this.tick(), this.state.interval);
    }

    stopGame() {
        this.setState({
            started: false,
        });

        clearInterval(this._timer);
    }

    changeNumColsHandler(event) {
        this.setState({
            size: {
                cols: event.target.value,
                rows: this.state.size.rows,
            },
        });
    }

    changeNumRowsHandler(event) {
        this.setState({
            size: {
                cols: this.state.size.cols,
                rows: event.target.value,
            },
        });
    }


    toggleAlive(i,j) {
        const cellStr = `pos-${i}-${j}`;
        const { alive } = this.state;
        if (alive.has(cellStr)) {
            alive.delete(cellStr);
        } else {
            alive.add(cellStr);
        }
        this.setState({
            alive,
        });
    }

    changeInitialCountHandler(event) {
        this.setState({
            initialCount: event.target.value,
        });
    }

    initializeBoard () {
        this.setState({ alive: new Set(), generation: 0 });
        this.makeRandomBoard(this.state.initialCount);
    }

    reset() {
        this.setState({
            alive: new Set(),
            generation: 0,
        });
    }

    changeIntervalHandler(event) {
        this.setState({
            interval: event.target.value,
        })
    }

    tick() {
        const { alive, generation } = this.state;
        const { cols: nCols, rows: nRows } = this.state.size;
        const newAlive = new Set();
        for (let i = 0; i < nCols; i++){
            for (let j = 0; j < nCols; j++){
                const cellStr = `pos-${i}-${j}`;

                const neighbours = this.getNeighbours(i,j);
                const numAliveNeighbours = intersection(alive, neighbours).size;
                if (alive.has(cellStr)) {
                    // Don't Need to check for Rules 1 and 3 since the alive set
                    // starts of empty.
                    // RULE 2:
                    if (numAliveNeighbours === 2 || numAliveNeighbours === 3) {
                        newAlive.add(cellStr);
                    }
                } else {
                    // RULE 4:
                    if (numAliveNeighbours === 3) {
                        newAlive.add(cellStr);
                    }
                }
            }
        }
        this.setState({
            alive: newAlive,
            generation: generation + 1,
        })
    }

  render() {
    return (
      <div>
          <h3>Conway's Game of Life</h3>
          <Controls
              initialCount={this.state.initialCount}
              changeInitialCountHandler={this.changeInitialCountHandler}
              initializeHandler={this.initializeBoard}
              startHandler={this.startGame}
              stopHandler={this.stopGame}
              nCols={this.state.size.cols}
              changeNumColsHandler={this.changeNumColsHandler}
              nRows={this.state.size.rows}
              changeNumRowsHandler={this.changeNumRowsHandler}
              resetHandler={this.reset}
              interval={this.state.interval}
              changeIntervalHandler={this.changeIntervalHandler}
              tickOnce={this.tick}
          />
          <div class="text-data">Generation: {this.state.generation}</div>
          <Grid
              nRows={this.state.size.rows}
              nCols={this.state.size.cols}
              alive={this.state.alive}
              changeHandler={this.toggleAlive}
          />
      </div>
    );
  }
}

export default GoL;
