import React, { Component } from 'react';


class Controls extends Component {

  render() {
    const {
        initialCount,
        changeInitialCountHandler,
        startHandler,
        stopHandler,
        initializeHandler,
        nCols,
        changeNumColsHandler,
        nRows,
        changeNumRowsHandler,
        interval,
        changeIntervalHandler,
        resetHandler,
        tickOnce,
    } = this.props;
    return (
      <div class="game-controls">
          <label>Initial Count</label>
          <input value={initialCount} onChange={changeInitialCountHandler} />
          <label>Number of Columns</label>
          <input value={nCols} onChange={changeNumColsHandler} />
          <label>Number of Rows</label>
          <input value={nRows} onChange={changeNumRowsHandler} />
          <label>Tick Interval</label>
          <input value={interval} onChange={changeIntervalHandler} />
          <button className="neutral" onClick={initializeHandler}>Initialise</button>
          <button className="cancel" onClick={resetHandler}>Reset</button>
          <button className="action" onClick={startHandler}>Start</button>
          <button className="warning" onClick={stopHandler}>Stop</button>
          <button className="neutral" onClick={tickOnce}>Step Once</button>
      </div>
    );
  }
}

export default Controls;
