import React, { Component } from 'react';
import Cell from './Cell';


class Grid extends Component {
    isAlive(i, j) {
        const cellStr = `pos-${i}-${j}`
        return this.props.alive.has(cellStr);
    }

    render() {
        const cells = [];
        let contents;

        const { nCols, nRows } = this.props;
        let row;
        let cols;
        for (let i = 0; i < nRows; i++) {
            cols = [];
            for (let j = 0; j < nCols; j++ ) {
                const activeClass = this.isAlive(i,j) ? ' alive' : ''
                cols.push(
                    <Cell
                        className={`life-grid-cell${activeClass}`}
                        key={`cell-${i}-${j}`}
                        pos={{ i, j }}
                        changeHandler={this.props.changeHandler}
                    />
                );
            }
            row = <div className="life-grid-row" key={`row-${i}`}>{cols}</div>
            cells.push(row);
        }
        contents = (
            <div className='life-grid'>
                {cells}
            </div>
        );

        return (
            <React.Fragment>
                {contents}
            </React.Fragment>
        );
    }
}

export default Grid;
